
import { computed, defineComponent, ref, watch } from 'vue';
import IconView from '../iconview/IconView.vue';
import { formatDate } from './utils/DateUtils';

export default defineComponent({
  name: 'DateInput',
  components: {
    IconView,
  },
  props: {
    selectedDate: {
      type: Date as new () => Date,
    },
    resetTypedDate: {
      type: [Date as new () => Date],
    },
    format: {
      type: [String, Function],
    },
    translation: {
      type: Object,
    },
    inline: {
      type: Boolean,
    },
    id: {
      type: String,
    },
    name: {
      type: String,
    },
    openDate: {
      type: Date,
    },
    placeholder: {
      type: String,
    },
    inputClass: {
      type: String,
    },
    clearButton: {
      type: Boolean,
    },
    clearButtonIcon: {
      type: String,
    },
    calendarButton: {
      type: Boolean,
    },
    calendarButtonIcon: {
      type: String,
    },
    calendarButtonIconContent: {
      type: String,
    },
    disabled: {
      type: Boolean,
    },
    required: {
      type: Boolean,
    },
    typeable: {
      type: Boolean,
    },
    addBootstrapClass: {
      type: Boolean,
    },
    useUtc: {
      type: Boolean,
    },
    minimumView: {
      type: String,
      default: 'day',
    },
    maximumView: {
      type: String,
      default: 'year',
    },
    hideInput: {
      type: Boolean,
      default: true,
    },
    fullMonthName: {
      type: Boolean,
      default: false,
    },
    iconColor: {
      default: 'black',
      type: String,
    },
    iconHeight: {
      default: 16,
      type: [String, Number],
    },
    iconWidth: {
      default: 16,
      type: [String, Number],
    },
    theme: {
      default: 'green',
      type: String,
    },
  },
  emits: ['show-calendar', 'typed-date', 'clear-date', 'close-calendar'],
  setup(props, { emit }) {
    const typedDate = ref<string | number>();
    const inputRef = ref(null);
    // computed
    const computedInputClass = computed(() => {
      if (props.addBootstrapClass) {
        if (typeof props.inputClass === 'string') {
          return [props.inputClass, 'form-control'].join(' ');
        }
        // tbd : need to add here props.inputClass
        return {
          'form-control': true,
        };
      }
      return props.inputClass;
    });

    const formattedValue = computed((): string | Date | null | number => {
      if (!props.selectedDate) {
        return null;
      }
      if (typedDate.value) {
        return typedDate.value;
      }

      let date =
        typeof props.format === 'function'
          ? props.format(props.selectedDate)
          : formatDate(new Date(props.selectedDate), props.format as any, props.translation as any);

      if (props.minimumView === props.maximumView) {
        const [, y, z] = date.split(' ');
        if (props.maximumView === 'month') {
          if (props.fullMonthName) {
            const i = props.translation?.monthsAbbr.indexOf(y);
            return props.translation?.months[i];
          }
          date = y;
        } else if (props.maximumView === 'year') {
          date = z;
        }
      }

      return date;
    });

    // watchers
    watch(
      () => props.resetTypedDate,
      () => {
        typedDate.value = '';
      }
    );

    /**
     * open Calendar
     */
    function showCalendar(): void {
      emit('show-calendar');
    }

    /**
     * Attempt to parse a typed date
     * @param {Event} event
     */
    function parseTypedDate(event: KeyboardEvent): void {
      if (
        [
          27, // escape
          13, // enter
        ].includes(event.keyCode)
      ) {
        (inputRef.value as any).blur();
      }
      if (props.typeable) {
        const { value } = inputRef.value as any;
        const temptypedDate = Date.parse(value);
        if (!Number.isNaN(temptypedDate)) {
          typedDate.value = value;
          emit('typed-date', new Date(temptypedDate));
        }
      }
    }

    /**
     * emit a clearDate event
     */
    function clearDate(): void {
      emit('clear-date');
    }

    /**
     * nullify the typed date to defer to regular formatting
     * called once the input is blurred
     */
    function inputBlurred(): void {
      if (props.typeable && Number.isNaN(Date.parse((inputRef.value as any).value))) {
        clearDate();
        // need to check this if required
        (inputRef.value as any).value = null;
        typedDate.value = '';
      }
      emit('close-calendar', true);
    }

    return {
      typedDate,
      computedInputClass,
      formattedValue,
      showCalendar,
      parseTypedDate,
      inputBlurred,
      inputRef,
    };
  },
});
